import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'ugw-steps-option-form-wizard',
  templateUrl: './steps-option-form-wizard.component.html'
})
export class StepsOptionFormWizardComponent implements OnInit {

  showStep1 = false;
  showPrevStep2 = true;
  isValidStep3 = true;
  showNextStep4 = true;
  constructor() { }

  ngOnInit() {
  }

  onBtnShowStep1Click() {
    this.showStep1 = !this.showStep1;
  }

  onBtnShowPrevStep2Click() {
    this.showPrevStep2 = !this.showPrevStep2;
  }

  onBtnIsValidStep3Click() {
    this.isValidStep3 = !this.isValidStep3;
  }

  onBtnShowNextStep4Click() {
    this.showNextStep4 = !this.showNextStep4;
  }

}
