import { Dangerdialog2Component } from './../../../../../../../projects/uiigateway/pilar/src/lib/components/_utilities/danger-dialog2/danger-dialog2.component';
import { Component, OnInit } from "@angular/core";
import { BsModalService } from "ngx-bootstrap";
import { Confirmdialog2Component } from "projects/uiigateway/pilar/src/lib/components/_utilities/confirm-dialog2/confirm-dialog2.component";
import { Warningdialog2Component } from "projects/uiigateway/pilar/src/lib/components/_utilities/warning-dialog2/warning-dialog2.component";
import { Successdialog2Component } from "projects/uiigateway/pilar/src/lib/components/_utilities/success-dialog2/success-dialog2.component";

@Component({
  selector: "ugw-confirm-dialog",
  templateUrl: "./confirm-dialog2.component.html",
  styleUrls: ["./confirm-dialog2.component.scss"],
})
export class ConfirmDialog2Component implements OnInit {
  constructor(private modalSvc: BsModalService) {}

  ngOnInit() {}

  displaySuccessDialog() {
    const modalDialog = this.modalSvc.show(Successdialog2Component, {
      class: "modal-alert modal-success2 modal-content",
    });

    (<Successdialog2Component>modalDialog.content).showConfirmationModal({
      title: "File Successfully Uploaded",
      message: "Please check status menu to view submission detail updates",
      confirmBtnlabel: "OK",
    });

    (<Successdialog2Component>modalDialog.content).onClose.subscribe(
      (result) => {
        
      }
    );
  }

  displayConfirmDialog() {
    const modalDialog = this.modalSvc.show(Confirmdialog2Component, {
      class: "modal-alert modal-confirm2 modal-content",
    });

    (<Confirmdialog2Component>modalDialog.content).showConfirmationModal({
      title: "Save",
      message: "Are you sure to confirm this action?",
      cancelBtnLabel: "No",
      confirmBtnlabel: "Yes",
    });

    (<Confirmdialog2Component>modalDialog.content).onClose.subscribe(
      (result) => {
        
      }
    );
  }

  displayWarningDialog() {
    const modalDialog = this.modalSvc.show(Warningdialog2Component, {
      class: "modal-alert modal-warning2 modal-content",
    });

    (<Warningdialog2Component>modalDialog.content).showConfirmationModal({
      title: "An Error Occured",
      message: "Please check your input again",
      confirmBtnlabel: "OK",
    });

    (<Warningdialog2Component>modalDialog.content).onClose.subscribe(
      (result) => {
        if (result === true) {
        } else {
        }
      }
    );
  }

  displayDangerDialog() {
    const modalDialog = this.modalSvc.show(Dangerdialog2Component, {
      class: "modal-alert modal-danger2 modal-content",
    });

    (<Dangerdialog2Component>modalDialog.content).showConfirmationModal({
      title: "This Action is Forbidden",
      message: "Please contact your administrator for further guidance",
      confirmBtnlabel: "OK",
    });

    (<Dangerdialog2Component>modalDialog.content).onClose.subscribe(
      (result) => {
       
      }
    );
  }
}
