import { CustomHtmlAccordionComponent } from './sections/custom-html-accordion/custom-html-accordion.component';
import { DisableAccordionComponent } from './sections/disable-accordion/disable-accordion.component';
import { OpeningEventAccordionComponent } from './sections/opening-event-accordion/opening-event-accordion.component';
import { AnimationAccordionComponent } from './sections/animation-accordion/animation-accordion.component';
import { BasicAccordionComponent } from './sections/basic-accordion/basic-accordion.component';
import { Component } from '@angular/core';
import { ShowcaseRightNavService } from '../../showcase-right-nav.service';
import { InitiallyOpenedAccordionComponent } from './sections/initially-opened-accordion/initially-opened-accordion.component';
import { DynamicAccordionComponent } from './sections/dynamic-accordion/dynamic-accordion.component';
import { DynamicBodyAccordionComponent } from './sections/dynamic-body-accordion/dynamic-body-accordion.component';
import { ManualToggleAccordionComponent } from './sections/manual-toggle-accordion/manual-toggle-accordion.component';
import { OtherCloseAccordionComponent } from './sections/other-close-accordion/other-close-accordion.component';
import { StylingAccordionComponent } from './sections/styling-accordion/styling-accordion.component';
declare var require: any;

@Component({
  selector: 'ugw-doc-accordion',
  templateUrl: './doc-accordion.component.html',
  styleUrls: ['./doc-accordion.component.scss']
})
export class DocAccordionComponent {
  apiDocSection: any;
  description: string;
  accordionSection: Array<any>;
  title: string;

  constructor(private rightMenuSvc: ShowcaseRightNavService) {
    this.initializeMetaData();
    this.initializeApiDocumentation();
    this.initializeSection();
    this.setRightMenu();
  }

  private initializeApiDocumentation() {
    this.apiDocSection = {
      title: this.title.toLowerCase(),
      selector: 'pil-accordion',
      properties: [
        {
          name: 'heading',
          type: 'string',
          default: '',
          description: 'Provide default header text.'
        },
        {
          name: 'isAnimated',
          type: 'boolean',
          default: 'false',
          description: 'Expand/collapse group panels with animation.'
        },
        {
          name: 'isDisabled',
          type: 'boolean',
          default: 'false',
          description: 'Disable group panels.'
        },
        {
          name: 'isOpen',
          type: 'boolean',
          default: 'false',
          description: 'Initially open group panels.'
        },
        {
          name: 'closeOthers',
          type: 'boolean',
          default: 'false',
          deacription: 'Only open one group panel at a time.'
        },
        {
          name: 'panelColor',
          type: 'string (default, primary, secondary & tertiary)',
          default: 'default',
          description: 'Panel color options.'
        }
      ],
      events: [
        {
          name: 'isOpenChange',
          type: 'EventEmitter<>',
          default: '',
          description: 'Event trigerred when a group is clicked.'
        }
      ]
    };
  }

  private initializeMetaData() {
    this.title = 'Accordion';
    this.description = 'Accordion is a component we can use to push notifications that will be displayed at the top right corner of our screen.';
  }

  private initializeSection() {
    this.accordionSection = [
      {
        anchor: 'basic',
        title: 'Basic',
        desc: 'Click header to expand/collapse the content.',
        component: BasicAccordionComponent,
        markup: require('!!raw-loader!./sections/basic-accordion/basic-accordion.component.html'),
        typescript: require('!!raw-loader!./sections/basic-accordion/basic-accordion.component.ts')
      },
      {
        anchor: 'animation',
        title: 'With animation',
        desc: 'Use input property isAnimated to enable/disable animation.',
        component: AnimationAccordionComponent,
        markup: require('!!raw-loader!./sections/animation-accordion/animation-accordion.component.html'),
        typescript: require('!!raw-loader!./sections/animation-accordion/animation-accordion.component.ts')
      },
      {
        anchor: 'opening-event',
        title: 'Group opening event',
        desc: 'Accordion with isOpenChange event listener.',
        component: OpeningEventAccordionComponent,
        markup: require('!!raw-loader!./sections/opening-event-accordion/opening-event-accordion.component.html'),
        typescript: require('!!raw-loader!./sections/opening-event-accordion/opening-event-accordion.component.ts')
      },
      {
        anchor: 'disabled',
        title: 'Disabled',
        desc: 'Use input property isDisabled to disable group panels.',
        component: DisableAccordionComponent,
        markup: require('!!raw-loader!./sections/disable-accordion/disable-accordion.component.html'),
        typescript: require('!!raw-loader!./sections/disable-accordion/disable-accordion.component.ts')
      },
      {
        anchor: 'intially-opened',
        title: 'Initially opened',
        desc: 'Use input property isOpen to initially opened group panels',
        component: InitiallyOpenedAccordionComponent,
        markup: require('!!raw-loader!./sections/initially-opened-accordion/initially-opened-accordion.component.html'),
        typescript: require('!!raw-loader!./sections/initially-opened-accordion/initially-opened-accordion.component.ts')
      },
      {
        anchor: 'dynamic',
        title: 'Dynamic accordion',
        desc: '',
        component: DynamicAccordionComponent,
        markup: require('!!raw-loader!./sections/dynamic-accordion/dynamic-accordion.component.html'),
        typescript: require('!!raw-loader!./sections/dynamic-accordion/dynamic-accordion.component.ts')
      },
      {
        anchor: 'dynamic-body-content',
        title: 'Dynamic body content',
        desc: '',
        component: DynamicBodyAccordionComponent,
        markup: require('!!raw-loader!./sections/dynamic-body-accordion/dynamic-body-accordion.component.html'),
        typescript: require('!!raw-loader!./sections/dynamic-body-accordion/dynamic-body-accordion.component.ts')
      },
      {
        anchor: 'manual-toggle',
        title: 'Manual toggle',
        desc: '',
        component: ManualToggleAccordionComponent,
        markup: require('!!raw-loader!./sections/manual-toggle-accordion/manual-toggle-accordion.component.html'),
        typescript: require('!!raw-loader!./sections/manual-toggle-accordion/manual-toggle-accordion.component.ts')
      },
      {
        anchor: 'other-close',
        title: 'Open only one at a time',
        desc: 'Use input property closeOthers to open one group at a time.',
        component: OtherCloseAccordionComponent,
        markup: require('!!raw-loader!./sections/other-close-accordion/other-close-accordion.component.html'),
        typescript: require('!!raw-loader!./sections/other-close-accordion/other-close-accordion.component.ts')
      },
      {
        anchor: 'custom-html',
        title: 'Custom HTML',
        desc: 'If you want a custom heading, just simply inject uii-accordion-heading inside accordion group and ignore input property heading.',
        component: CustomHtmlAccordionComponent,
        markup: require('!!raw-loader!./sections/custom-html-accordion/custom-html-accordion.component.html'),
        typescript: require('!!raw-loader!./sections/custom-html-accordion/custom-html-accordion.component.ts')
      },
      {
        anchor: 'styling',
        title: 'Styling',
        desc: 'There are 4 types of accordion which are default, primary, secondary, tertiary.',
        component: StylingAccordionComponent,
        markup: require('!!raw-loader!./sections/styling-accordion/styling-accordion.component.html'),
        typescript: require('!!raw-loader!./sections/styling-accordion/styling-accordion.component.ts')
      }
    ];
  }

  private setRightMenu() {
    const apiDocs = [{
      anchor: 'accordion-api-docs',
      title: 'API documentation',
      desc: null,
      component: null,
      markup: null,
      typescript: null
    }];
    const customRightMenu  = this.accordionSection.concat(apiDocs);
    this.rightMenuSvc.setRightMenu(customRightMenu);
  }

}
