import { Component, OnInit } from '@angular/core';
import { ToastService } from 'pilar';

@Component({
  selector: 'ugw-basic-toast',
  templateUrl: './basic-toast.component.html',
  styleUrls: ['./basic-toast.component.scss']
})
export class BasicToastComponent {

  constructor(private toastSvc: ToastService) { }

  pushSuccessToast(): void {
    this.toastSvc.success('wohoo, this toast is working.', 'success', {
      timeOut: 5000
    });
  }

  pushWarningToast(): void {
    this.toastSvc.warning('wohoo, this toast is working.', 'warning',  {
      timeOut: 5000
    });
  }

  pushErrorToast(): void {
    this.toastSvc.error('wohoo, this toast is working.', 'error',  {
      timeOut: 5000
    });
  }

  pushInfoToast(): void {
    this.toastSvc.info('wohoo, this toast is working.', 'info',  {
      timeOut: 5000
    });
  }

}
