import { TableWithCustomActionComponent } from './sections/table-with-custom-action/table-with-custom-action.component';
import { TableWithActionComponent } from './sections/table-with-action/table-with-action.component';
import { SummaryRowTableComponent } from './sections/summary-row-table/summary-row-table.component';
import { Component, OnInit } from '@angular/core';
import { BasicTableComponent } from './sections/basic-table/basic-table.component';
import { ShowcaseRightNavService } from '../../showcase-right-nav.service';
import { TableWithCheckboxComponent } from './sections/table-with-checkbox/table-with-checkbox.component';
import { ColumnDefinitionsTableComponent } from './sections/column-definitions/column-definitions-table.component';

declare var require: any;

@Component({
  selector: 'ugw-doc-infobox',
  templateUrl: './doc-table.component.html',
  styleUrls: ['./doc-table.component.scss']
})
export class DocTableComponent implements OnInit {
  apiDocSection: any;
  description: string;
  tableSection: Array<any>;
  title: string;

  constructor(private rightMenuSvc: ShowcaseRightNavService) {
  }

  ngOnInit() {
    this.initializeMetaData();
    this.initializeApiDocumentation();
    this.initializeSection();
    this.setRightMenu();
  }

  private initializeMetaData() {
    this.title = 'Table';
    this.description = `
      Documentation and examples for Pilar Table component.
      The component is used to display list of data in a table,
      but with a lot of functionality out-of-the-box like sorting,
      searching, paging, etc.
    `;
  }

  private initializeApiDocumentation() {
    this.apiDocSection = {
      title: this.title.toLowerCase(),
      selector: 'pil-table',
      properties: [
        {
          name: 'columns',
          type: 'array',
          default: '[]',
          description: `
            The column should be an object that contains at least "name" & "prop" properties.
            You may specify column "type" in one of: label, html, input-text, date, input-radio.
            Other column possible properties: hide_in_mobile, inputType, inputMaxLength,
            textUppercase, textLowercase.
          `
        },
        {
          name: 'rows',
          type: 'array',
          default: '[]',
          description: 'The list of data that needs to display in the table.'
        },
        {
          name: 'isAllCheckbox',
          type: 'boolean',
          default: false,
          description: `
            Set this to "true" if you want the check-all-box to
            check the checkbox in all pages, not just the current page.
          `
        }
      ],
      events: [
        {
          name: 'none',
          type: 'none',
          default: 'none',
          description: ''
        }
      ]
    };
  }

  private initializeSection() {
    this.tableSection = [
      {
        anchor: 'basic',
        title: 'Basic',
        desc: 'This is a basic table implementation',
        component: BasicTableComponent,
        markup: require('!!raw-loader!./sections/basic-table/basic-table.component.html'),
        typescript: require('!!raw-loader!./sections/basic-table/basic-table.component')
      },
      {
        anchor: 'column-definitions',
        title: 'Column Definitions',
        desc: 'Available column definitions of the table.',
        component: ColumnDefinitionsTableComponent,
        markup: require('!!raw-loader!./sections/column-definitions/column-definitions-table.component.html'),
        typescript: require('!!raw-loader!./sections/column-definitions/column-definitions-table.component')
      },
      {
        anchor: 'table-with-action',
        title: 'Table with action',
        desc: 'Display the action in the table.',
        component: TableWithActionComponent,
        markup: require('!!raw-loader!./sections/table-with-action/table-with-action.component.html'),
        typescript: require('!!raw-loader!./sections/table-with-action/table-with-action.component')
      },
      {
        anchor: 'table-with-custom-action',
        title: 'Table with custom action',
        desc: 'Display the custom action in the table.',
        component: TableWithCustomActionComponent,
        markup: require('!!raw-loader!./sections/table-with-custom-action/table-with-custom-action.component.html'),
        typescript: require('!!raw-loader!./sections/table-with-custom-action/table-with-custom-action.component')
      },
      {
        anchor: 'table-with-checkbox',
        title: 'Table with checkbox',
        desc: 'Display the checkbox in the table.',
        component: TableWithCheckboxComponent,
        markup: require('!!raw-loader!./sections/table-with-checkbox/table-with-checkbox.component.html'),
        typescript: require('!!raw-loader!./sections/table-with-checkbox/table-with-checkbox.component')
      },
      {
        anchor: 'table-with-summary',
        title: 'Table with summary',
        desc: 'Summary rows at the bottom of the table.',
        component: SummaryRowTableComponent,
        markup: require('!!raw-loader!./sections/summary-row-table/summary-row-table.component.html'),
        typescript: require('!!raw-loader!./sections/summary-row-table/summary-row-table.component')
      },
    ];
  }

  private setRightMenu() {
    const apiDocs = [{
      anchor: 'table-api-docs',
      title: 'API documentation',
      desc: null,
      component: null,
      markup: null,
      typescript: null
    }];
    const customRightMenu = this.tableSection.concat(apiDocs);
    this.rightMenuSvc.setRightMenu(customRightMenu);
  }
}
