import { Component, OnInit, Renderer2 } from '@angular/core';
import { ElementRefService } from './element-ref.service';
import { MENU } from './showcase-menu';

@Component({
  selector: 'ugw-showcase',
  templateUrl: './showcase.component.html'
})
export class ShowcaseComponent implements OnInit {
  clientWidthIsMobile: boolean;
  isTop = false;
  menus: Array<any>;
  rightNavElements = [];
  sectionElements = [];
  showRightNav: boolean;

  constructor(
    private renderer: Renderer2,
    private elementRefSvc: ElementRefService
  ) {}

  ngOnInit() {
    this.menus = MENU;
    this.showRightNav = document.documentElement.clientWidth > 768;
    this.clientWidthIsMobile = document.documentElement.clientWidth < 768;
    this.getRightNavElementRef();
    this.getSectionElementRef();
    this.windowEventListener();
  }

  windowEventListener() {
    window.addEventListener('scroll', () => {
      const scrollPosition = window.scrollY;
      this.isTop = scrollPosition > 80;
      if (this.rightNavElements.length > 0 && document.documentElement.clientWidth >= 1200) {
        this.updateActiveNav((scrollPosition + 100), 'active');
      }
    });
    window.addEventListener('resize', () => {
      this.clientWidthIsMobile = document.documentElement.clientWidth < 768;
      if (this.clientWidthIsMobile === true) {
        this.showRightNav = false;
      }
    });
  }

  onMenuItemClick() {
    if (this.clientWidthIsMobile === true) {
      this.showRightNav = false;
    }
  }

  getRightNavElementRef() {
    this.elementRefSvc.getRightNavCollectionRef().subscribe(elementRef => {
      this.rightNavElements = [];
      elementRef.forEach((element: any, index: number) => {
        if (index === 0) {
          this.renderer.addClass(element.nativeElement, 'active');
        }
        this.rightNavElements.push(element);
      });
    });
  }

  getSectionElementRef() {
    this.elementRefSvc.getSectionTitleCollectionRef().subscribe(elementRef => {
      this.sectionElements = elementRef.map(item => item.nativeElement.offsetTop);
    });
  }

  updateActiveNav(scrollPosition: number, className: string) {
    this.sectionElements.forEach((sectionOffsetTop: number, i) => {
      if (scrollPosition >= sectionOffsetTop) {
        if ((i - 1) >= 0) {
          this.renderer.removeClass(this.rightNavElements[(i - 1)].nativeElement, className);
        }
        this.renderer.addClass(this.rightNavElements[i].nativeElement, className);
      } else {
        this.renderer.removeClass(this.rightNavElements[i].nativeElement, className);
        if (i === 0) {
          this.renderer.addClass(this.rightNavElements[i].nativeElement, className);
        }
      }
    });
  }

}
