import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';

@Component({
  selector: 'ugw-demo-header',
  templateUrl: './demo-header.component.html',
  styleUrls: ['./demo-header.component.scss']
})
export class DemoHeaderComponent implements OnInit {
  @Input()
  set showSidebar(value: boolean) {
    this._showSidebar = value;
  }
  @Output() showSidebarChange = new EventEmitter();
  isTop = false;
  _showSidebar: boolean;

  constructor() { }

  ngOnInit() {
    window.addEventListener('scroll', () => {
      this.isTop = window.scrollY > 80;
    });
  }

  onHumbergerButtonClick() {
    this._showSidebar = !this._showSidebar;
    this.showSidebarChange.emit(this._showSidebar);
  }

}
