import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'ugw-table-with-custom-action',
  templateUrl: './table-with-custom-action.component.html',
  styleUrls: ['./table-with-custom-action.component.scss']
})
export class TableWithCustomActionComponent implements OnInit {

  rows: Array<any> = [];
  columns: Array<any> = [];
  actionSelected: string;
  rowSelected: string;

  constructor() { }

  ngOnInit() {
    this.columns = [
      {
        name: 'No',
        prop: 'number'
      },
      {
        name: 'Name',
        prop: 'name'
      },
      {
        name: 'Age',
        prop: 'age'
      },
    ];

    this.rows = [
      {
        name: 'Marta Mccoy',
        age: 31,
        additional_action: [
          {
            icon_class: 'fa fa-file',
            tooltip: 'File',
            click: (row) => {
              this.actionSelected = 'file';
              this.rowSelected = row;
            }
          },
          {
            icon_class: 'fa fa-folder',
            tooltip: 'Archive',
            click: (row) => {
              this.actionSelected = 'archive';
              this.rowSelected = row;
            }
          },
        ]
      },
      {
        name: 'Fanny Holman',
        age: 23,
        additional_action: [
          {
            icon_class: 'fa fa-folder',
            tooltip: 'Archive',
            click: (row) => {
              this.actionSelected = 'archive';
              this.rowSelected = row;
            }
          },
        ]
      },
    ];
  }

}
