import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'ugw-table-with-action',
  templateUrl: './table-with-action.component.html',
  styleUrls: ['./table-with-action.component.scss']
})
export class TableWithActionComponent implements OnInit {

  rows: Array<any> = [];
  columns: Array<any> = [];
  actionSelected: string;
  rowSelected: string;

  constructor() { }

  ngOnInit() {
    this.columns = [
      {
        name: 'No',
        prop: 'number'
      },
      {
        name: 'Name',
        prop: 'name'
      },
      {
        name: 'Age',
        prop: 'age'
      },
    ];

    this.rows = [
      {
        name: 'Marta Mccoy',
        age: 31,
        auth: {
          canRead: true,
          canUpdate: true,
          canPrint: true,
        }
      },
      {
        name: 'Fanny Holman',
        age: 23,
        auth: {
          canDelete: true,
          canActivate: true,
        }
      },
      {
        name: 'Latonya Gibson',
        age: 22,
        auth: {
          canPublish: true,
          canValidate: true,
        }
      },
      {
        name: 'Ines Wiggins',
        age: 35,
        auth: {
          canExport: true,
          canImport: true,
        }
      },
    ];
  }

  onDetailClicked(row) {
    this.actionSelected = 'detail';
    this.rowSelected = row;
  }

  onEditClicked(row) {
    this.actionSelected = 'edit';
    this.rowSelected = row;
  }

  onPrintClicked(row) {
    this.actionSelected = 'print';
    this.rowSelected = row;
  }

  onDeleteClicked(row) {
    this.actionSelected = 'delete';
    this.rowSelected = row;
  }

  onSwitchClicked(row) {
    this.actionSelected = 'switch';
    this.rowSelected = row;
  }

  onPublishClicked(row) {
    this.actionSelected = 'publish';
    this.rowSelected = row;
  }

  onValidateClicked(row) {
    this.actionSelected = 'validate';
    this.rowSelected = row;
  }

  onExportClicked(row) {
    this.actionSelected = 'export';
    this.rowSelected = row;
  }

  onImportClicked(row) {
    this.actionSelected = 'import';
    this.rowSelected = row;
  }

}
