import { Component } from '@angular/core';
import { ShowcaseRightNavService } from '../../showcase-right-nav.service';
declare var require: any;

import { BasicFileUploadComponent } from './sections/basic-file-upload/basic-file-upload.component';
import { MultipleFileUploadComponent } from './sections/multiple-file-upload/multiple-file-upload.component';
import { SizeFileUploadComponent } from './sections/size-file-upload/size-file-upload.component';
import { TypeFileUploadComponent } from './sections/type-file-upload/type-file-upload.component';
import { EventsFileUploadComponent } from './sections/events-file-upload/events-file-upload.component';
import { PrevPositionFileUploadComponent } from './sections/prev-position-file-upload/prev-position-file-upload.component';
import { SetFileUploadComponent } from './sections/set-file-upload/set-file-upload.component';

@Component({
  selector: 'ugw-doc-file-upload',
  templateUrl: './doc-file-upload.component.html',
  styleUrls: ['./doc-file-upload.component.scss']
})
export class DocFileUploadComponent {
  apiDocSection: any;
  description: string;
  fileUploadSections: Array<any>;
  title: string;

  constructor(private rightMenuSvc: ShowcaseRightNavService) {
    this.initializeMetaData();
    this.initializeApiDocumentation();
    this.initializeSection();
    this.setRightMenu();
  }

  private initializeApiDocumentation() {
    this.apiDocSection = {
      title: this.title.toLowerCase(),
      selector: 'pil-file-upload',
      properties: [
        {
          name: 'inputLabel',
          type: 'string',
          default: 'Pilih berkas',
          description: 'Label of upload file button'
        },
        {
          name: 'fileType',
          type: 'string',
          default: '.csv, .xlsx, application/pdf',
          description: 'Type of file to upload'
        },
        {
          name: 'maxFileSize',
          type: 'number',
          default: '1',
          description: 'Maximum size of file to upload, this property depends on maxFileType'
        },
        {
          name: 'showFileSize',
          type: 'boolean',
          default: 'true',
          description: 'Hide or show size of the file'
        },
        {
          name: 'maxFileType',
          type: 'string',
          default: 'KB',
          description: 'Size type'
        },
        {
          name: 'previewPosition',
          type: 'string',
          default: 'bottom',
          description: 'Set the placement of file preview, this property just for single file upload'
        },
        {
          name: 'showPreview',
          type: 'boolean',
          default: 'true',
          description: 'Hide or show file preview'
        },
        {
          name: 'multiple',
          type: 'boolean',
          default: 'false',
          description: 'Muiltiple means you can upload more than one file'
        },
        {
          name: 'fileList',
          type: 'Array<any>',
          default: '[ ]',
          description: 'List of files from your component into send to upload component'
        }
      ],
      events: [
        {
          name: 'filesSelected',
          type: 'EventEmitter<>',
          default: 'null',
          description: 'Fired when selected files changed'
        }
      ]
    };
  }

  private initializeMetaData() {
    this.title = 'File upload';
    this.description = 'Upload your file';
  }

  private initializeSection() {
    this.fileUploadSections = [
      {
        anchor: 'basic',
        title: 'Basic',
        desc: 'Basic file upload implementation',
        component: BasicFileUploadComponent,
        markup: require('!!raw-loader!./sections/basic-file-upload/basic-file-upload.component.html'),
        typescript: require('!!raw-loader!./sections/basic-file-upload/basic-file-upload.component.ts')
      },
      {
        anchor: 'file-type',
        title: 'File type',
        desc: 'You can specify the file type',
        component: TypeFileUploadComponent,
        markup: require('!!raw-loader!./sections/type-file-upload/type-file-upload.component.html'),
        typescript: require('!!raw-loader!./sections/type-file-upload/type-file-upload.component.ts')
      },
      {
        anchor: 'file-size',
        title: 'File size',
        desc: 'You can specify the maximum size of file',
        component: SizeFileUploadComponent,
        markup: require('!!raw-loader!./sections/size-file-upload/size-file-upload.component.html'),
        typescript: require('!!raw-loader!./sections/size-file-upload/size-file-upload.component.ts')
      },
      {
        anchor: 'multiple-file',
        title: 'Multiple files',
        desc: 'You can upload multiple files',
        component: MultipleFileUploadComponent,
        markup: require('!!raw-loader!./sections/multiple-file-upload/multiple-file-upload.component.html'),
        typescript: require('!!raw-loader!./sections/multiple-file-upload/multiple-file-upload.component.ts')
      },
      {
        anchor: 'preview-position',
        title: 'Preview position',
        desc: 'If you upload a single file, you can specify the position of preview',
        component: PrevPositionFileUploadComponent,
        markup: require('!!raw-loader!./sections/prev-position-file-upload/prev-position-file-upload.component.html'),
        typescript: require('!!raw-loader!./sections/prev-position-file-upload/prev-position-file-upload.component.ts')
      },
      {
        anchor: 'set-file',
        title: 'Set files',
        desc: 'You can set the files from your component',
        component: SetFileUploadComponent,
        markup: require('!!raw-loader!./sections/set-file-upload/set-file-upload.component.html'),
        typescript: require('!!raw-loader!./sections/set-file-upload/set-file-upload.component.ts')
      },
      {
        anchor: 'file-upload-event',
        title: 'Selected files event',
        desc: 'Triggered when files selected',
        component: EventsFileUploadComponent,
        markup: require('!!raw-loader!./sections/events-file-upload/events-file-upload.component.html'),
        typescript: require('!!raw-loader!./sections/events-file-upload/events-file-upload.component.ts')
      }
    ];
  }

  private setRightMenu() {
    const apiDocs = [{
      anchor: 'file-upload-api-docs',
      title: 'API documentation',
      desc: null,
      component: null,
      markup: null,
      typescript: null
    }];
    const customRightMenu  = this.fileUploadSections.concat(apiDocs);
    this.rightMenuSvc.setRightMenu(customRightMenu);
  }

}
