import { Component, OnInit } from "@angular/core";
import { BsModalService } from "ngx-bootstrap";
import { ConfirmdialogComponent } from "projects/uiigateway/pilar/src/lib/components/_utilities/confirm-dialog/confirm-dialog.component";

@Component({
  selector: "ugw-confirm-dialog",
  templateUrl: "./confirm-dialog.component.html",
  styleUrls: ["./confirm-dialog.component.scss"],
})
export class ConfirmDialogComponent implements OnInit {
  constructor(private modalSvc: BsModalService) {}

  ngOnInit() {}

  displayModal() {
    const modalDialog = this.modalSvc.show(ConfirmdialogComponent, {
      class: "modal-alert modal-warning modal-content",
    });

    (<ConfirmdialogComponent>modalDialog.content).showConfirmationModal({
      title: "Save",
      message: "Are you sure to confirm this action?",
      cancelBtnLabel: "No",
      confirmBtnlabel: "Yes",
    });

    (<ConfirmdialogComponent>modalDialog.content).onClose.subscribe(
      (result) => {}
    );
  }
}
