import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'ugw-demo-sidebar',
  templateUrl: './demo-sidebar.component.html',
  styleUrls: ['./demo-sidebar.component.scss']
})
export class DemoSidebarComponent implements OnInit {
  @Input()
  set showSideBar(value: any) {
    this._showSidebar = value;
  }
  @Input() menus: any = [];
  @Output() menuItemClick = new EventEmitter();
  _showSidebar = false;

  constructor(private router: Router) {}

  onMenuCLick(item: any, i: number) {
    this.menus.forEach((element: any, index: number) => {
      element.active = index === i ? true : false;
    });
    this.menuItemClick.emit(true);
    this.router.navigate([item.url]);
  }
  ngOnInit() {
  }

  isMenuActive(itemUrl: string) {
    if (itemUrl === 'installation') {
      itemUrl = '';
    }
    const url = this.router.url.toString().split('#');
    return url[0].replace(/\//g, '') === itemUrl;
  }

}
