import { LabelingButtonImageUploadComponent } from './sections/labeling-button-image-upload/labeling-button-image-upload.component';
import { Component, OnInit } from '@angular/core';
import {ShowcaseRightNavService} from '../../showcase-right-nav.service';
import {BasicImageUploadComponent} from './sections/basic-image-upload/basic-image-upload.component';
import {HideDeleteImageUploadComponent} from './sections/hide-delete-image-upload/hide-delete-image-upload.component';
declare var require: any;


@Component({
  selector: 'ugw-doc-image-upload',
  templateUrl: './doc-image-upload.component.html',
  styleUrls: ['./doc-image-upload.component.scss']
})
export class DocImageUploadComponent implements OnInit {
  apiDocSection: any;
  description: string;
  imageUploadSection: Array<any>;
  title: string;

  constructor(private rightMenuSvc: ShowcaseRightNavService)  { }

  ngOnInit() {
    this.initializeMetaData();
    this.initializeApiDocumentation();
    this.initializeSection();
    this.setRightMenu();
  }

  private initializeMetaData() {
    this.title = 'Image Upload';
    this.description = 'Image Upload is used to upload image with extention .jpg, .jpeg, .png and you can view image was uploaded.';
  }
  private initializeApiDocumentation() {
    this.apiDocSection = {
      title: this.title.toLowerCase(),
      selector: 'pil-image-upload',
      properties: [
        {
          name: 'inputLabel',
          type: 'string',
          default: 'Upload',
          description: 'Set your label button of image.'
        },
        {
          name: 'label',
          type: 'string',
          default: '',
          description: 'Set your label of image.'
        },
        {
          name: 'caption',
          type: 'string',
          default: '',
          description: 'Set your caption in image.'
        },
        {
          name: 'required',
          type: 'boolean',
          default: 'false',
          description: 'Set your required of file image upload.'
        },
        {
          name: 'fileType',
          type: 'Array<string>',
          default: '[jpg, jpeg, png]',
          description: 'Set your type of image.'
        },
        {
          name: 'imaxFileSize',
          type: 'string',
          default: '5B',
          description: 'Set your maximum file size.'
        },
        {
          name: 'fileTypeAllowed',
          type: 'string',
          default: '',
          description: 'Set your type file to allowed.'
        },
        {
          name: 'fileOversize',
          type: 'boolean',
          default: '',
          description: 'Set your oversize.'
        },
        {
          name: 'fileNotAllowed',
          type: 'boolean',
          default: '',
          description: 'Set your allowed file.'
        },
        {
          name: 'hideIconDelete',
          type: 'boolean',
          default: 'false',
          description: 'Set your allowed file.'
        }
      ],
      events: [
        {
          name: 'imageSelected',
          type: 'EventEmitter<>',
          default: 'null',
          description: ''
        }
      ]
    };
  }

  private initializeSection() {
    this.imageUploadSection = [
      {
        anchor: 'basic',
        title: 'Basic',
        desc: 'This is a basic image upload implementation.',
        component: BasicImageUploadComponent,
        markup: require('!!raw-loader!./sections/basic-image-upload/basic-image-upload.component.html'),
        typescript: require('!!raw-loader!./sections/basic-image-upload/basic-image-upload.component.ts')
      },
      {
        anchor: 'hide',
        title: 'Hide icon delete',
        desc: 'You can hide icon delete when you upload the image.',
        component: HideDeleteImageUploadComponent,
        markup: require('!!raw-loader!./sections/basic-image-upload/basic-image-upload.component.html'),
        typescript: require('!!raw-loader!./sections/basic-image-upload/basic-image-upload.component.ts')
      },
      {
        anchor: 'label',
        title: 'Label ',
        desc: 'You can make label in button and you can choose type file alloed',
        component: LabelingButtonImageUploadComponent,
        markup: require('!!raw-loader!./sections/labeling-button-image-upload/labeling-button-image-upload.component.html'),
        typescript: require('!!raw-loader!./sections/labeling-button-image-upload/labeling-button-image-upload.component.ts')
      }
    ];
  }
  private setRightMenu() {
    const apiDocs = [{
      anchor: 'image-upload-api-docs',
      title: 'API documentation',
      desc: null,
      component: null,
      markup: null,
      typescript: null
    }];
    const customRightMenu  = this.imageUploadSection.concat(apiDocs);
    this.rightMenuSvc.setRightMenu(customRightMenu);
  }

}
