import { Component, OnInit } from '@angular/core';
declare var require: any;

import { BasicTabsetComponent } from './sections/basic-tabset/basic-tabset.component';
import { ShowcaseRightNavService } from '../../showcase-right-nav.service';
import { TabsetWithIconTextComponent } from './sections/tabset-with-icon-text/tabset-with-icon-text.component';
import { TabsetWithIconComponent } from './sections/tabset-with-icon/tabset-with-icon.component';
import { ManuallyTabSelectionComponent } from './sections/manually-tab-selection/manually-tab-selection.component';
import { TabsetSelectEventComponent } from './sections/tabset-select-event/tabset-select-event.component';
import { TabsetDeselectEventComponent } from './sections/tabset-deselect-event/tabset-deselect-event.component';

@Component({
  selector: 'ugw-doc-tabset',
  templateUrl: './doc-tabset.component.html',
  styleUrls: ['./doc-tabset.component.scss']
})
export class DocTabsetComponent {
  apiDocSection: any;
  description: string;
  tabsetSection: Array<any>;
  title: string;

  constructor(private rightMenuSvc: ShowcaseRightNavService) {
    this.initializeMetaData();
    this.initializeApiDocumentation();
    this.initializeSection();
    this.setRightMenu();
  }

  private initializeApiDocumentation() {
    this.apiDocSection = {
      title: this.title.toLowerCase(),
      selector: 'pil-tabset',
      properties: [
        {
          name: 'title',
          type: 'string',
          default: '',
          description: 'Size of the calendar and entire components. Can be medium which is default or large.'
        },
        {
          name: 'tabIcon',
          type: 'string',
          default: '',
          description: 'Size of the calendar and entire components. Can be medium which is default or large.'
        },
        {
          name: 'tabMobileAttr',
          type: 'string',
          default: 'both',
          description: 'Size of the calendar and entire components. Can be medium which is or large.'
        }
      ],
      events: [
        {
          name: 'selected',
          type: 'EventEmitter<>',
          default: '',
          description: 'event yang terjadi ketika halaman nomor halaman dipilih'
        },
        {
          name: 'deselected',
          type: 'EventEmitter<>',
          default: '',
          description: 'event yang terjadi ketika halaman nomor halaman dipilih'
        }
      ]
    };
  }

  private initializeMetaData() {
    this.title = 'Tabset';
    this.description = 'Used to group content into different tab in one page';
  }

  private initializeSection() {
    this.tabsetSection = [
      {
        anchor: 'basic',
        title: 'Basic',
        desc: 'This is a basic tabset implementation',
        component: BasicTabsetComponent,
        markup: require('!!raw-loader!./sections/basic-tabset/basic-tabset.component.html'),
        typescript: require('!!raw-loader!./sections/basic-tabset/basic-tabset.component.ts')
      },
      {
        anchor: 'tabset-with-icon',
        title: 'Tabset with icon',
        desc: 'You can use icon in every tab header',
        component: TabsetWithIconComponent,
        markup: require('!!raw-loader!./sections/tabset-with-icon/tabset-with-icon.component.html'),
        typescript: require('!!raw-loader!./sections/tabset-with-icon/tabset-with-icon.component.ts')
      },
      {
        anchor: 'tabset-with-icon-text',
        title: 'Tabset with icon and text',
        desc: 'You can use icon and text in every tab header',
        component: TabsetWithIconTextComponent,
        markup: require('!!raw-loader!./sections/tabset-with-icon-text/tabset-with-icon-text.component.html'),
        typescript: require('!!raw-loader!./sections/tabset-with-icon-text/tabset-with-icon-text.component.ts')
      },
      {
        anchor: 'manually-tab-selection',
        title: 'Manually tab selection',
        desc: 'You can select tab form your component',
        component: ManuallyTabSelectionComponent,
        markup: require('!!raw-loader!./sections/manually-tab-selection/manually-tab-selection.component.html'),
        typescript: require('!!raw-loader!./sections/manually-tab-selection/manually-tab-selection.component.ts')
      },
      {
        anchor: 'tabset-select-event',
        title: 'Select event',
        desc: 'You can do something when tab is selected',
        component: TabsetSelectEventComponent,
        markup: require('!!raw-loader!./sections/tabset-select-event/tabset-select-event.component.html'),
        typescript: require('!!raw-loader!./sections/tabset-select-event/tabset-select-event.component.ts')
      },
      {
        anchor: 'tabset-deselect-event',
        title: 'Deselect event',
        desc: 'You can do something when tab is deselected',
        component: TabsetDeselectEventComponent,
        markup: require('!!raw-loader!./sections/tabset-deselect-event/tabset-deselect-event.component.html'),
        typescript: require('!!raw-loader!./sections/tabset-deselect-event/tabset-deselect-event.component.ts')
      }
    ];
  }

  private setRightMenu() {
    const apiDocs = [{
      anchor: 'tabset-api-docs',
      title: 'API documentation',
      desc: null,
      component: null,
      markup: null,
      typescript: null
    }];
    const customRightMenu  = this.tabsetSection.concat(apiDocs);
    this.rightMenuSvc.setRightMenu(customRightMenu);
  }

}
