import { Component, OnInit, OnDestroy } from "@angular/core";
import { BsModalRef } from "ngx-bootstrap/modal";
import { Subject } from "rxjs";

@Component({
  selector: "uii-warning-dialog2",
  templateUrl: "./warning-dialog2.component.html",
  styleUrls: ["./warning-dialog2.component.scss"],
})
export class Warningdialog2Component implements OnInit {
  public confirmBtnlabel: string;
  public active = false;
  public message: string;
  public title: string;
  public onClose: Subject<boolean>;

  constructor(public _activeModal: BsModalRef) {}

  public ngOnInit(): void {
    this.onClose = new Subject();
  }

  public showConfirmationModal(config: any): void {
    this.title = config.title ? config.title : "";
    this.message = config.message ? config.message : "";
    this.confirmBtnlabel = config.confirmBtnlabel ? config.confirmBtnlabel : "";
    this.active = true;
  }

  public onConfirm(): void {
    this.active = false;
    this.onClose.next(true);
    this._activeModal.hide();
  }

  public hideConfirmationModal(): void {
    this.active = false;
    this.onClose.next(null);
    this._activeModal.hide();
  }
}
