import { Component, Input, ViewChild, ComponentFactoryResolver } from '@angular/core';
import { ShowcaseDirective } from '../../showcase.directive';

@Component({
  selector: 'ugw-docs-section',
  template: `<ng-template ugwShowCaseDirective></ng-template>`
})
export class DocsSectionComponent  {
  @Input()
  set demoComponent(component: any) {
    const componentFactory = this.componentFactoryResolver.resolveComponentFactory(component);
    this.adHost.viewContainerRef.createComponent(componentFactory);
  }
  @ViewChild(ShowcaseDirective) adHost: ShowcaseDirective;

  constructor(private componentFactoryResolver: ComponentFactoryResolver) { }

}
