import { Component, Input, ViewChildren, QueryList, AfterViewInit } from '@angular/core';
import { ElementRefService } from './../../element-ref.service';

@Component({
  selector: 'ugw-section',
  templateUrl: './section.component.html'
})
export class SectionComponent implements AfterViewInit {
  @ViewChildren('sectionTitleRef') sectionTitle: QueryList<any>;
  @Input() sections: any;
  @Input()
  set apiDocSections(apiDocumentations: any) {
    apiDocumentations.title = apiDocumentations.title.split(' ').join('-');
    this._apiDocSections = apiDocumentations;
  }

  _apiDocSections: any;

  constructor(private elementRefSvc: ElementRefService) { }

  ngAfterViewInit(): void {
    setTimeout(() => {
      this.elementRefSvc.setSectionTitleCollectionRef(this.sectionTitle);
    });
  }

}
