import { Component } from '@angular/core';
import { SetPageComponent } from './sections/set-page/set-page.component';
import { ShowcaseRightNavService } from '../../showcase-right-nav.service';
declare var require: any;

import { PageChangeEventComponent } from './sections/page-change-event/page-change-event.component';
import { BasicPaginationComponent } from './sections/basic-pagination/basic-pagination.component';
@Component({
  selector: 'ugw-doc-pagination',
  templateUrl: './doc-pagination.component.html',
  styleUrls: ['./doc-pagination.component.scss']
})
export class DocPaginationComponent {
  apiDocSection: any;
  description: string;
  paginationSection: Array<any>;
  title: string;

  constructor(private rightMenuSvc: ShowcaseRightNavService) {
    this.initializeMetaData();
    this.initializeApiDocumentation();
    this.initializeSection();
    this.setRightMenu();
  }

  private initializeApiDocumentation() {
    this.apiDocSection = {
      title: this.title.toLowerCase(),
      selector: 'pil-pagination',
      properties: [
        {
          name: 'amountOfData',
          type: 'number',
          default: '100',
          description: 'The sum of all available data.'
        },
        {
          name: 'dataPerPage',
          type: 'number',
          default: '10',
          description: 'Amount of data that will be displayed on one page.'
        },
        {
          name: 'pageTotal',
          type: 'number',
          default: '5',
          description: 'Limit of page number to be displayed.'
        }
      ],
      events: [
        {
          name: 'onPageChange',
          type: 'EventEmitter<>',
          default: '',
          description: 'Event trigered when page selected'
        }
      ]
    };
  }

  private initializeMetaData() {
    this.title = 'Pagination';
    this.description = 'Pagination is the way to divide a content into some separate pages.';
  }

  private initializeSection() {
    this.paginationSection = [
      {
        anchor: 'basic',
        title: 'Basic',
        desc: 'Basic pagination implementation',
        component: BasicPaginationComponent,
        markup: require('!!raw-loader!./sections/basic-pagination/basic-pagination.component.html'),
        typescript: require('!!raw-loader!./sections/basic-pagination/basic-pagination.component.ts')
      },
      {
        anchor: 'page-change-event',
        title: 'Page change event',
        desc: 'You can do some stuff when page selected',
        component: PageChangeEventComponent,
        markup: require('!!raw-loader!./sections/page-change-event/page-change-event.component.html'),
        typescript: require('!!raw-loader!./sections/page-change-event/page-change-event.component.ts')
      },
      {
        anchor: 'set-page',
        title: 'Set Page',
        desc: 'Set the page manualy from your component',
        component: SetPageComponent,
        markup: require('!!raw-loader!./sections/set-page/set-page.component.html'),
        typescript: require('!!raw-loader!./sections/set-page/set-page.component.ts')
      }
    ];
  }

  private setRightMenu() {
    const apiDocs = [{
      anchor: 'pagination-api-docs',
      title: 'API documentation',
      desc: null,
      component: null,
      markup: null,
      typescript: null
    }];
    const customRightMenu  = this.paginationSection.concat(apiDocs);
    this.rightMenuSvc.setRightMenu(customRightMenu);
  }
}
