import { InformationWithTagHtmlComponent } from './sections/information-with-tag-html/information-with-tag-html.component';

import { Component } from '@angular/core';
import { ShowcaseRightNavService } from '../../showcase-right-nav.service';
import { BasicInformationComponent } from './sections/basic-information/basic-information.component';
declare var require: any;

@Component({
  selector: 'ugw-doc-information',
  templateUrl: './doc-information.component.html',
  styleUrls: ['./doc-information.component.scss']
})
export class DocInformationComponent {
  apiDocSection: any;
  description: string;
  informationSection: Array<any>;
  title: string;

  constructor(private rightMenuSvc: ShowcaseRightNavService) {
    this.initializeMetaData();
    this.initializeApiDocumentation();
    this.initializeSection();
    this.setRightMenu();
  }

  private initializeApiDocumentation() {
    this.apiDocSection = {
      title: this.title.toLowerCase(),
      selector: 'pil-toast',
      properties: [
        {
          name: 'type',
          type: 'string',
          default: 'info',
          description: 'Type of a displayed information.'
        },
        {
          name: 'message',
          type: 'string',
          default: '',
          description: 'Message of a displayed information.'
        }
      ]
    };
  }

  private initializeMetaData() {
    this.title = 'Information';
    this.description = 'Information is a component we can use to display an additional information on a special case.';
  }

  private initializeSection() {
    this.informationSection = [
      {
        anchor: 'basic',
        title: 'Basic',
        desc: 'This is the basic implementation of information.',
        component: BasicInformationComponent,
        markup: require('!!raw-loader!./sections/basic-information/basic-information.component.html'),
        typescript: require('!!raw-loader!./sections/basic-information/basic-information.component.ts')
      },
      {
        anchor: 'With tag HTML',
        title: 'With tag HTML',
        desc: 'This is the basic implementation of information with Tag HTML.',
        component: InformationWithTagHtmlComponent,
        markup: require('!!raw-loader!./sections/information-with-tag-html/information-with-tag-html.component.html'),
        typescript: require('!!raw-loader!./sections/information-with-tag-html/information-with-tag-html.component.ts')
      },
    ];
  }

  private setRightMenu() {
    const apiDocs = [{
      anchor: 'information-api-docs',
      title: 'API documentation',
      desc: null,
      component: null,
      markup: null,
      typescript: null
    }];
    const customRightMenu  = this.informationSection.concat(apiDocs);
    this.rightMenuSvc.setRightMenu(customRightMenu);
  }

}
