import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'ugw-emit-event-paginate-change-form-wizard',
  templateUrl: './emit-event-paginate-change-form-wizard.component.html'
})
export class EmitEventPaginateChangeFormWizardComponent implements OnInit {

  currentStep = 0;
  cancelBtnClickCount = 0;
  nextBtnClickCount = 0;
  prevBtnClickCount = 0;
  submitBtnClickCount = 0;
  constructor() { }

  ngOnInit() {
  }

  onStepChange($event) {
    this.currentStep = $event.activeStepIndex;
  }

  onBtnCancelClick() {
    this.cancelBtnClickCount++;
  }

  onBtnNextClick() {
    this.nextBtnClickCount++;
  }

  onBtnPrevClick() {
    this.prevBtnClickCount++;
  }

  onBtnSubmitClick() {
    this.submitBtnClickCount++;
  }

}
