import { Component } from '@angular/core';

@Component({
  selector: 'ugw-delayed-switch',
  templateUrl: './delayed-switch.component.html'
})
export class DelayedSwitchComponent {

  checked = false;
  timeOut: any;

  onChangeEvent($event: boolean) {
    this.timeOut = 3;
    const interval = setInterval(() => {
      if (this.timeOut <= 1) {
        this.checked = !$event;
        clearInterval(interval);
      }
      this.timeOut --;
    }, 1000);
  }

}
