import { Component, OnInit } from '@angular/core';
import {ShowcaseRightNavService} from '../../showcase-right-nav.service';
import {BasicTooltipComponent} from './sections/basic-tooltip/basic-tooltip.component';
import {PlacementTooltipComponent} from './sections/placement-tooltip/placement-tooltip.component';
import {ParagraphTooltipComponent} from './sections/paragraph-tooltip/paragraph-tooltip.component';
import {DisabledTooltipComponent} from './sections/disabled-tooltip/disabled-tooltip.component';
import {DynamicHtmlTooltipComponent} from './sections/dynamic-html-tooltip/dynamic-html-tooltip.component';
declare var require: any;

@Component({
  selector: 'ugw-doc-tooltip',
  templateUrl: './doc-tooltip.component.html',
  styleUrls: ['./doc-tooltip.component.scss']
})
export class DocTooltipComponent implements OnInit {
  apiDocSection: any;
  description: string;
  tooltipSection: Array<any>;
  title: string;

  constructor(private rightMenuSvc: ShowcaseRightNavService) { }

  ngOnInit() {
    this.initializeMetaData();
    this.initializeApiDocumentation();
    this.initializeSection();
    this.setRightMenu();
  }

  private initializeMetaData() {
    this.title = 'Tooltip';
    this.description = 'Use these shorthand utilities for quickly displaying your tooltip message. You can set the placement on bottom, left, right and top';
  }
  private initializeApiDocumentation() {
    this.apiDocSection = {
      title: this.title.toLowerCase(),
      selector: 'pil-tooltip',
      properties: [
        {
          name: 'uiiTooltip',
          type: 'string',
          default: '',
          description: 'Set your tooltip message text here.'
        },
        {
          name: 'tooltipPlacement',
          type: 'string',
          default: 'Top',
          description: '"top|bottom|left|right" Indicates where the tooltip should be placed.'
        },
        {
          name: '[tooltipDisabled]',
          type: 'boolean',
          default: 'False',
          description: '"true|false" Indicates if tooltip should be disabled. If tooltip is disabled then it will not be shown'
        }
      ],
      events: [
        {
          name: '[uiiTooltip]',
          type: 'EventEmitter<>',
          default: 'none',
          description: 'To trigger your html view using tag <tooltip-content></tooltip-content>. In that tag, make sure you set #contentName. Look at the example dynamic html in tooltip'
        }
      ]
    };
  }

  private initializeSection() {
    this.tooltipSection = [
      {
        anchor: 'basic',
        title: 'Basic',
        desc: 'This is a basic tooltip implementation',
        component: BasicTooltipComponent,
        markup: require('!!raw-loader!./sections/basic-tooltip/basic-tooltip.component.html'),
        typescript: require('!!raw-loader!./sections/basic-tooltip/basic-tooltip.component.ts')
      },
      {
        anchor: 'placement-tooltip',
        title: 'Placement Tooltip',
        desc: 'This is where you can set the placement of tooltip implementation',
        component: PlacementTooltipComponent,
        markup: require('!!raw-loader!./sections/placement-tooltip/placement-tooltip.component.html'),
        typescript: require('!!raw-loader!./sections/placement-tooltip/placement-tooltip.component.ts')
      },
      {
        anchor: 'paragraph-tooltip',
        title: 'Tooltips in Paragraph',
        desc: 'You can use many tooltips in any paragraph. To differ set the text which having tooltip, you can use <span></span> tags each.',
        component: ParagraphTooltipComponent,
        markup: require('!!raw-loader!./sections/paragraph-tooltip/paragraph-tooltip.component.html'),
        typescript: require('!!raw-loader!./sections/paragraph-tooltip/paragraph-tooltip.component.ts')
      },
      {
        anchor: 'dynamic-html-tooltip',
        title: 'Dynamic html content in Tooltip',
        desc: 'Customize your own message in tooltip with a html content.',
        component: DynamicHtmlTooltipComponent,
        markup: require('!!raw-loader!./sections/dynamic-html-tooltip/dynamic-html-tooltip.component.html'),
        typescript: require('!!raw-loader!./sections/dynamic-html-tooltip/dynamic-html-tooltip.component.ts')
      },
      {
        anchor: 'disabled-tooltip',
        title: 'Disabled tooltip',
        desc: 'If you feel like to disable your tooltip',
        component: DisabledTooltipComponent,
        markup: require('!!raw-loader!./sections/disabled-tooltip/disabled-tooltip.component.html'),
        typescript: require('!!raw-loader!./sections/disabled-tooltip/disabled-tooltip.component.ts')
      }
    ];
  }
  private setRightMenu() {
    const apiDocs = [{
      anchor: 'tooltip-api-docs',
      title: 'API documentation',
      desc: null,
      component: null,
      markup: null,
      typescript: null
    }];
    const customRightMenu  = this.tooltipSection.concat(apiDocs);
    this.rightMenuSvc.setRightMenu(customRightMenu);
  }
}
