import { Component, OnInit } from "@angular/core";
import { BsModalRef, BsModalService } from "ngx-bootstrap";

@Component({
  selector: "ugw-basic-modal",
  templateUrl: "./basic-modal.component.html",
  styleUrls: ["./basic-modal.component.scss"],
})
export class BasicModalComponent implements OnInit {
  modalRef: any;

  constructor(private modalSvc: BsModalService) {}

  ngOnInit() {}

  displayModal() {
    const initialState = {};

    this.modalRef = this.modalSvc.show(BasicModalExampleComponent, {
      initialState,
      class: "modal-md",
    });
  }
}
@Component({
  selector: "ugw-basic-modal-example",
  template: `
    <div class="modal-header">
      <button
        type="button"
        class="close"
        aria-label="Close"
        (click)="activeModal.hide()"
      >
        <span aria-hidden="true">&times;</span>
      </button>
      <h5 class="">Modal Title</h5>
    </div>
    <div class="modal-body">Modal Content</div>
    <div class="modal-footer">
      <div class="button-action-center">
        <button
          (click)="onCloseModal()"
          type="button"
          class="btn btn-default"
          style="margin-bottom: 0px"
        >
          Close
        </button>
      </div>
    </div>
  `,
})
export class BasicModalExampleComponent implements OnInit {
  constructor(public activeModal: BsModalRef) {}

  ngOnInit() {}

  onCloseModal() {
    this.activeModal.hide();
  }
}
