import { Component, OnInit } from '@angular/core';
import { ShowcaseRightNavService } from '../../showcase-right-nav.service';
import { InformationPilarFontComponent } from './sections/information-pilar-font/information-pilar-font.component';
declare var require: any;

@Component({
  selector: 'ugw-doc-pilar-font',
  templateUrl: './doc-pilar-font.component.html',
  styleUrls: ['./doc-pilar-font.component.scss']
})
export class DocPilarFontComponent  {
  apiDocSection: any;
  title: string;
  description: string;
  pilarFont: Array<any>;

  constructor(
    private rightMenuSvc: ShowcaseRightNavService
  ) {
    this.initializeMetaData();
    this.initializeApiDocumentation();
    this.initializeSection();
    this.setRightMenu();
  }

  private initializeApiDocumentation() {
    // For API on Bottom
    this.apiDocSection = {
      title: this.title.toLowerCase(),
      selector: 'pilar-font',
      properties: [
        {
          name: 'pf-nameIcon',
          type: 'className',
          default: 'style',
          description: 'Call the className to use an icon'
        },
      ],
    };
  }

  private initializeMetaData() {
    this.title = 'Pilar Font';
    this.description = 'Pilar font created with <3';
  }

  private initializeSection() {
    this.pilarFont = [
      {
        anchor: 'information',
        title: 'Information',
        desc: 'How to use pilar fonts',
        component: InformationPilarFontComponent,
        markup: '<i class="pf-className"></i>',
        typescript: 'Does not require typescript'
      },
    ];
  }

  private setRightMenu() {
    const apiDocs = [{
      anchor: 'pilar-font',
      title: 'How to use',
      desc: null,
      component: null,
      markup: null,
      typescript: null
    }];
    const customRightMenu = this.pilarFont.concat(apiDocs);
    this.rightMenuSvc.setRightMenu(customRightMenu);
  }

}
