import { Component, OnInit, ViewChildren, QueryList } from '@angular/core';
import { Router, NavigationStart } from '@angular/router';
import { ShowcaseRightNavService } from '../../showcase-right-nav.service';
import { ElementRefService } from './../../element-ref.service';

@Component({
  selector: 'ugw-right-sidebar',
  templateUrl: './right-sidebar.component.html',
  styleUrls: ['./right-sidebar.component.scss']
})
export class RightSidebarComponent implements OnInit {
  menus: any;
  baseAnchor: any;
  @ViewChildren('menuRef') rightNavRef: QueryList<any>;

  constructor(
    private router: Router,
    private rightMenuSvc: ShowcaseRightNavService,
    private elementRefSvc: ElementRefService
  ) {
    this.baseAnchor = router.url.split('#')[0];
  }

  ngOnInit() {
    this.anchorSubscription();
    this.fetchRighMenu();
  }

  private anchorSubscription() {
    this.router.events.subscribe(event => {
      if (event instanceof NavigationStart) {
        this.baseAnchor = event.url.split('#')[0];
      }
    });
  }

  private fetchRighMenu() {
    this.rightMenuSvc.getRightMenu().subscribe(menus => {
      this.menus = menus;
      setTimeout(() => {
        this.elementRefSvc.setRightNavCollectionRef(this.rightNavRef);
      });
    });
  }

}
