import { Component, OnInit } from '@angular/core';
import { ToastService } from 'pilar';

@Component({
  selector: 'ugw-timeout-toast',
  templateUrl: './timeout-toast.component.html',
  styleUrls: ['./timeout-toast.component.scss']
})
export class TimeoutToastComponent implements OnInit {

  timeOut: number;

  constructor(private toastSvc: ToastService) { }

  ngOnInit() {
  }

  pushInfoToast() {
    this.toastSvc.info('wohooo, this toast is working.', 'info', {
      timeOut: this.timeOut
    });
  }

}
