import { Component, OnInit } from '@angular/core';
import {BasicInfoboxComponent} from './sections/basic-infobox/basic-infobox.component';
import {ColorInfoboxComponent} from './sections/color-infobox/color-infobox.component';
import {ShowcaseRightNavService} from '../../showcase-right-nav.service';
import {PageChangeEventComponent} from '../doc-pagination/sections/page-change-event/page-change-event.component';
import {GroupInfoboxComponent} from './sections/group-infobox/group-infobox.component';
declare var require: any;

@Component({
  selector: 'ugw-doc-infobox',
  templateUrl: './doc-infobox.component.html',
  styleUrls: ['./doc-infobox.component.scss']
})
export class DocInfoboxComponent implements OnInit {
  apiDocSection: any;
  description: string;
  infoboxSection: Array<any>;
  title: string;

  constructor(private rightMenuSvc: ShowcaseRightNavService) { }

  ngOnInit() {
    this.initializeMetaData();
    this.initializeApiDocumentation();
    this.initializeSection();
    this.setRightMenu();
  }

  private initializeMetaData() {
    this.title = 'Infobox';
    this.description = 'Use these shorthand utilities for quickly displaying your summary data. There are eight types of summary color in use only such as default, primary, success, info, danger , warning, dark, secondary. You can flexibly set the width fit to column and set your own icon.';
  }
  private initializeApiDocumentation() {
    this.apiDocSection = {
      title: this.title.toLowerCase(),
      selector: 'pil-infobox',
      properties: [
        {
          name: 'title',
          type: 'string',
          default: 'Title',
          description: 'Set your title of summary.'
        },
        {
          name: 'subtitle',
          type: 'string',
          default: 'Subtitle',
          description: 'Set your subtitle of summary value.'
        },
        {
          name: 'type',
          type: 'string',
          default: 'Default',
          description: 'Set your type of summary color.'
        },
        {
          name: 'infoIcon',
          type: 'string',
          default: '',
          description: 'Set your icon of summary.'
        }
      ],
      events: [
        {
          name: 'none',
          type: 'none',
          default: 'none',
          description: ''
        }
      ]
    };
  }

  private initializeSection() {
    this.infoboxSection = [
      {
        anchor: 'basic',
        title: 'Basic',
        desc: 'This is a basic infobox implementation',
        component: BasicInfoboxComponent,
        markup: require('!!raw-loader!./sections/basic-infobox/basic-infobox.component.html'),
        typescript: require('!!raw-loader!./sections/basic-infobox/basic-infobox.component.ts')
      },
      {
        anchor: 'group-infobox',
        title: 'Group Infobox',
        desc: 'This is example of group infobox to diplay more than 1 summary and different icon',
        component: GroupInfoboxComponent,
        markup: require('!!raw-loader!./sections/group-infobox/group-infobox.component.html'),
        typescript: require('!!raw-loader!./sections/group-infobox/group-infobox.component.ts')
      },
      {
        anchor: 'color-infobox',
        title: 'Color Infobox',
        desc: 'This is example of color infobox to diplay more than 1 summary',
        component: ColorInfoboxComponent,
        markup: require('!!raw-loader!./sections/color-infobox/color-infobox.component.html'),
        typescript: require('!!raw-loader!./sections/color-infobox/color-infobox.component.ts')
      },
    ];
  }
  private setRightMenu() {
    const apiDocs = [{
      anchor: 'infobox-api-docs',
      title: 'API documentation',
      desc: null,
      component: null,
      markup: null,
      typescript: null
    }];
    const customRightMenu  = this.infoboxSection.concat(apiDocs);
    this.rightMenuSvc.setRightMenu(customRightMenu);
  }
}
