import { Component, OnInit } from '@angular/core';
import { ToastService } from 'pilar';

@Component({
  selector: 'ugw-pause-onclick-toast',
  templateUrl: './pause-onclick-toast.component.html',
  styleUrls: ['./pause-onclick-toast.component.scss']
})
export class PauseOnclickToastComponent implements OnInit {

  timeout = 5000;

  constructor(private toastSvc: ToastService) { }

  ngOnInit() {
  }

  pushInfoToast(): void {
    this.toastSvc.info('click this toast to prevent being removed!', 'info', {
      timeOut: this.timeout,
      removeOnClick: true
    });
  }

}
