import { Component, HostBinding, ViewContainerRef } from '@angular/core';
import { ToastService } from 'pilar';

@Component({
  selector: 'ugw-root',
  template: `<router-outlet></router-outlet>`
})
export class AppComponent {
  constructor(
    private toastSvc: ToastService,
    private viewContainerRef: ViewContainerRef
  ) {
    this.toastSvc.setToastRootViewContainerRef(this.viewContainerRef);
    this.toastSvc.addToastDynamicComponent();
  }
  @HostBinding('class.has-sidebar') public hasSidebar: boolean;
  @HostBinding('class.has-breadcrumb') public hasBreadcrumb: boolean;
  @HostBinding('class.is-home') public isHome: boolean;
}
