import { Component, OnInit } from '@angular/core';
import { ShowcaseRightNavService } from '../../showcase-right-nav.service';

@Component({
  selector: 'ugw-installation',
  templateUrl: './installation.component.html',
  styleUrls: ['./installation.component.scss']
})
export class InstallationComponent implements OnInit {

  installation = 'npm install pilar --registry https://npm.uii.ac.id';
  import = `import { PilarModule } from 'pilar';
  @NgModule({
      imports: [
          PilarModule.forRoot()
      ],
  });`;
  useStyle = `"styles": [
    "./node_modules/pilar/assets/stylesheets/styles.scss"
  ],`;

  constructor(private rightMenuSvc: ShowcaseRightNavService) { }

  ngOnInit() {
    this.rightMenuSvc.setRightMenu([]);
  }

}
