import { ConfirmDialogComponent } from "./sections/confirm-dialog/confirm-dialog.component";
import { BasicModalComponent } from "./sections/basic-modal/basic-modal.component";
import { Component, OnInit } from "@angular/core";
import { ShowcaseRightNavService } from "../../showcase-right-nav.service";
import { ConfirmDialog2Component } from "./sections/confirm-dialog2/confirm-dialog2.component";
declare var require: any;

@Component({
  selector: "ugw-doc-modal",
  templateUrl: "./doc-modal.component.html",
  styleUrls: ["./doc-modal.component.scss"],
})
export class DocModalComponent implements OnInit {
  apiDocSection: any;
  description: string;
  modalSection: Array<any>;
  title: string;

  constructor(private rightMenuSvc: ShowcaseRightNavService) {}

  ngOnInit() {
    this.initializeMetaData();
    this.initializeApiDocumentation();
    this.initializeSection();
    this.setRightMenu();
  }

  private initializeMetaData() {
    this.title = "Modal";
    this.description =
      "Use these shorthand utilities to display floating modal components to add dialogs to your site for lightboxes, user notifications, or completely custom content";
  }

  private initializeApiDocumentation() {
    this.apiDocSection = {
      title: this.title.toLowerCase(),
      selector: "pil-infobox",
      properties: [
        {
          name: "title",
          type: "string",
          default: "Title",
          description: "Set your modal title.",
        },
        {
          name: "message",
          type: "string",
          default: "Subtitle",
          description: "Set your modal message.",
        },
        {
          name: "cancleBtnLabel",
          type: "string",
          default: "Default",
          description: "Set your text on cancel button.",
        },
        {
          name: "confirmBtnLabel",
          type: "string",
          default: "",
          description: "Set your text on confirm button.",
        },
      ],
      events: [
        {
          name: "none",
          type: "none",
          default: "none",
          description: "",
        },
      ],
    };
  }

  private initializeSection() {
    this.modalSection = [
      {
        anchor: "basic",
        title: "Basic",
        desc: "This is a basic modal implementation",
        component: BasicModalComponent,
        markup: require("!!raw-loader!./sections/basic-modal/basic-modal.component.html"),
        typescript: require("!!raw-loader!./sections/basic-modal/basic-modal.component.ts"),
      },
      {
        anchor: "confirm",
        title: "Confirm",
        desc: "This is a confirm modal dialog implementation",
        component: ConfirmDialogComponent,
        markup: require("!!raw-loader!./sections/confirm-dialog/confirm-dialog.component.html"),
        typescript: require("!!raw-loader!./sections/confirm-dialog/confirm-dialog.component.ts"),
      },
      {
        anchor: "confirm2",
        title: "Confirm 2",
        desc: "This is a confirm modal 2nd design implementation",
        component: ConfirmDialog2Component,
        markup: require("!!raw-loader!./sections/confirm-dialog2/confirm-dialog2.component.html"),
        typescript: require("!!raw-loader!./sections/confirm-dialog2/confirm-dialog2.component.ts"),
      },
    ];
  }

  private setRightMenu() {
    const apiDocs = [
      {
        anchor: "modal-api-docs",
        title: "API documentation",
        desc: null,
        component: null,
        markup: null,
        typescript: null,
      },
    ];
    const customRightMenu = this.modalSection.concat(apiDocs);
    this.rightMenuSvc.setRightMenu(customRightMenu);
  }
}
