import { DisableChipComponent } from './sections/disable-chip/disable-chip.component';
import { BasicChipComponent } from './sections/basic-chip/basic-chip.component';
import { ShowcaseRightNavService } from './../../showcase-right-nav.service';
import { Component } from '@angular/core';
import { VerticalChipComponent } from './sections/vertical-chip/vertical-chip.component';
import { HorizontalChipComponent } from './sections/horizontal-chip/horizontal-chip.component';
declare var require: any;
@Component({
  selector: 'ugw-doc-chips',
  templateUrl: './doc-chips.component.html',
  styleUrls: ['./doc-chips.component.scss']
})
export class DocChipsComponent {
  apiDocSection: any;
  title: string;
  description: string;
  chipsSections: Array<any>;

  constructor(
    private rightMenuSvc: ShowcaseRightNavService
  ) {
    this.initializeMetaData();
    this.initializeApiDocumentation();
    this.initializeSection();
    this.setRightMenu();
  }

  private initializeApiDocumentation() {
    // For API on Bottom
    this.apiDocSection = {
      title: this.title.toLowerCase(),
      selector: 'chips',
      properties: [
        {
          name: '[items]',
          type: 'Array',
          default: 'null',
          description: 'Provide default items of chip.'
        },
        {
          name: '[disabled]',
          type: 'booelan',
          default: 'false',
          description: 'Disable all options on some of form'
        },
        {
          name: 'position',
          type: 'string',
          default: 'horizontal',
          description: 'Change position chip (vertical | horizontal)'
        },
      ],
      events: [
        {
          name: '(sendData)',
          type: 'EventEmitter<>',
          default: 'null',
          description: 'Fired when selected files changed'
        }
      ]
    };
  }

  private initializeMetaData() {
    this.title = 'Chips';
    this.description = 'A chip is a component that is basically like a checkbox';
  }

  private initializeSection() {
    this.chipsSections = [
      {
        anchor: 'basic',
        title: 'Basic',
        desc: 'Click on options from them',
        component: BasicChipComponent,
        markup: require('!!raw-loader!./sections/basic-chip/basic-chip.component.html'),
        typescript: require('!!raw-loader!./sections/basic-chip/basic-chip.component.ts')
      },
      {
        anchor: 'disable',
        title: 'Disable',
        desc: 'Disable chip',
        component: DisableChipComponent,
        markup: require('!!raw-loader!./sections/disable-chip/disable-chip.component.html'),
        typescript: require('!!raw-loader!./sections/disable-chip/disable-chip.component.ts')
      },
      {
        anchor: 'vertical',
        title: 'Vertical',
        desc: 'Vertical chip',
        component: VerticalChipComponent,
        markup: require('!!raw-loader!./sections/vertical-chip/vertical-chip.component.html'),
        typescript: require('!!raw-loader!./sections/vertical-chip/vertical-chip.component.ts')
      },
      {
        anchor: 'horizontal',
        title: 'Horizontal',
        desc: 'Hertical chip',
        component: HorizontalChipComponent,
        markup: require('!!raw-loader!./sections/horizontal-chip/horizontal-chip.component.html'),
        typescript: require('!!raw-loader!./sections/horizontal-chip/horizontal-chip.component.ts')
      },

    ];
  }

  private setRightMenu() {
    const apiDocs = [{
      anchor: 'chips-api-docs',
      title: 'API documentation',
      desc: null,
      component: null,
      markup: null,
      typescript: null
    }];
    const customRightMenu = this.chipsSections.concat(apiDocs);
    this.rightMenuSvc.setRightMenu(customRightMenu);
  }

}
