import { Component } from '@angular/core';
declare var require: any;
import { ShowcaseRightNavService } from '../../showcase-right-nav.service';
import { BasicSwitchComponent } from './sections/basic-switch/basic-switch.component';
import { ChangedSwitchComponent } from './sections/changed-switch/changed-switch.component';
import { CheckedSwitchComponent } from './sections/checked-switch/checked-switch.component';
import { DisabledSwitchComponent } from './sections/disabled-switch/disabled-switch.component';
import { DelayedSwitchComponent } from './sections/delayed-switch/delayed-switch.component';

@Component({
  selector: 'ugw-doc-switch',
  templateUrl: './doc-switch.component.html',
  styleUrls: ['./doc-switch.component.scss']
})
export class DocSwitchComponent {
  apiDocSection: any;
  description: string;
  switchSection: Array<any>;
  title: string;

  constructor(private rightMenuSvc: ShowcaseRightNavService) {
    this.initializeMetaData();
    this.initializeApiDocumentation();
    this.initializeSection();
    this.setRightMenu();
  }

  private initializeApiDocumentation() {
    this.apiDocSection = {
      title: this.title.toLowerCase(),
      selector: 'pil-switch',
      properties: [
        {
          name: 'checked',
          type: 'boolean',
          default: 'false',
          description: 'Check or uncheck switch'
        },
        {
          name: 'disabled',
          type: 'boolean',
          default: 'false',
          description: 'Enable or disable switch'
        },
        {
          name: 'useOverlay',
          type: 'boolean',
          default: 'false',
          description: 'With useOverlay, you can delay the checked of value depending on certain conditions'
        }
      ],
      events: [
        {
          name: 'changed',
          type: 'EventEmitter<>',
          default: '',
          description: 'Fired when swith`s value changed and useOverlay is false'
        },
        {
          name: 'changeEvent',
          type: 'EventEmitter<>',
          default: '',
          description: 'Fired when swith`s value changed and useOverlay is true'
        }
      ]
    };
  }

  private initializeMetaData() {
    this.title = 'Switch';
    this.description = 'Switch is used to toggle a boolean value';
  }

  private initializeSection() {
    this.switchSection = [
      {
        anchor: 'basic',
        title: 'Basic',
        desc: 'Basic switch implementation',
        component: BasicSwitchComponent,
        markup: require('!!raw-loader!./sections/basic-switch/basic-switch.component.html'),
        typescript: require('!!raw-loader!./sections/basic-switch/basic-switch.component.ts')
      },
      {
        anchor: 'disabled',
        title: 'Disabled',
        desc: 'Disable switch from your component',
        component: DisabledSwitchComponent,
        markup: require('!!raw-loader!./sections/disabled-switch/disabled-switch.component.html'),
        typescript: require('!!raw-loader!./sections/disabled-switch/disabled-switch.component.ts')
      },
      {
        anchor: 'checked',
        title: 'Checked',
        desc: 'Checked or unchecked switch from you component',
        component: CheckedSwitchComponent,
        markup: require('!!raw-loader!./sections/checked-switch/checked-switch.component.html'),
        typescript: require('!!raw-loader!./sections/checked-switch/checked-switch.component.ts')
      },
      {
        anchor: 'delayed',
        title: 'Delay status',
        desc: 'You can delay the switch checked value base on some condition',
        component: DelayedSwitchComponent,
        markup: require('!!raw-loader!./sections/delayed-switch/delayed-switch.component.html'),
        typescript: require('!!raw-loader!./sections/delayed-switch/delayed-switch.component.ts')
      },
      {
        anchor: 'changed',
        title: 'Changed',
        desc: `Fired when switch's checked status change and useOverlay is false`,
        component: ChangedSwitchComponent,
        markup: require('!!raw-loader!./sections/changed-switch/changed-switch.component.html'),
        typescript: require('!!raw-loader!./sections/changed-switch/changed-switch.component.ts')
      }
    ];
  }

  private setRightMenu() {
    const apiDocs = [{
      anchor: 'switch-api-docs',
      title: 'API documentation',
      desc: null,
      component: null,
      markup: null,
      typescript: null
    }];
    const customRightMenu  = this.switchSection.concat(apiDocs);
    this.rightMenuSvc.setRightMenu(customRightMenu);
  }

}
