import { Component } from '@angular/core';

@Component({
  selector: 'ugw-dynamic-accordion',
  templateUrl: './dynamic-accordion.component.html',
  styleUrls: ['./dynamic-accordion.component.scss']
})
export class DynamicAccordionComponent {

  groups = [
    {
      heading: 'Static header',
      content: 'This is static header content.'
    }
  ];

  constructor() { }

  addGroup(): void {
    this.groups.push({
      heading: 'Static header',
      content: 'This is static header content.'
    });
  }

}
