import { Component } from '@angular/core';
import { ShowcaseRightNavService } from '../../showcase-right-nav.service';
declare var require: any;

import { BasicToastComponent } from './sections/basic-toast/basic-toast.component';
import { BlankToastComponent } from './sections/blank-toast/blank-toast.component';
import { TimeoutToastComponent } from './sections/timeout-toast/timeout-toast.component';
import { PauseOnclickToastComponent } from './sections/pause-onclick-toast/pause-onclick-toast.component';

@Component({
  selector: 'ugw-doc-toast',
  templateUrl: './doc-toast.component.html',
  styleUrls: ['./doc-toast.component.scss']
})
export class DocToastComponent {
  apiDocSection: any;
  description: string;
  toastSection: Array<any>;
  title: string;

  constructor(private rightMenuSvc: ShowcaseRightNavService) {
    this.initializeMetaData();
    this.initializeApiDocumentation();
    this.initializeSection();
    this.setRightMenu();
  }

  private initializeApiDocumentation() {
    this.apiDocSection = {
      title: this.title.toLowerCase(),
      selector: 'pil-toast',
      properties: [
        {
          name: 'description',
          type: 'string',
          default: '',
          description: 'Description of a displayed toast.'
        },
        {
          name: 'title (optional)',
          type: 'string',
          default: '',
          description: 'Title of a displayed toast.'
        },
        {
          name: 'config (optional)',
          type: '{timeOut: number, removeOnClick: boolean}',
          default: 'timeOut',
          description: `Configuration for toast's time out and remove on click.`
        }
      ],
      events: [
        {
          name: 'success',
          type: '(description: string, title?: string, config?: {timeOut: number, removeOnClick: boolean}, callback?: () => void) => void',
          description: 'Event triggered when button clicked.'
        },
        {
          name: 'warning',
          type: '(description: string, title?: string, config?: {timeOut: number, removeOnClick: boolean}, callback?: () => void) => void',
          description: 'Event triggered when button clicked.'
        },
        {
          name: 'error',
          type: '(description: string, title?: string, config?: {timeOut: number, removeOnClick: boolean}, callback?: () => void) => void',
          description: 'Event triggered when button clicked.'
        },
        {
          name: 'info',
          type: '(description: string, title?: string, config?: {timeOut: number, removeOnClick: boolean}, callback?: () => void) => void',
          description: 'Event triggered when button clicked.'
        }
      ]
    };
  }

  private initializeMetaData() {
    this.title = 'Toast';
    this.description = 'Toast is a component we can use to push notifications that will be displayed at the top right corner of our screen.';
  }

  private initializeSection() {
    this.toastSection = [
      {
        anchor: 'usage',
        title: 'Usage',
        desc: 'We need to dynamically add toast to our app component by adding ViewContainerRef as an argument to setToastRootViewContainerRef and invoke addToastDynamicComponent from ToastService afterwards inside of constructor in our app component.',
        component: BlankToastComponent,
        typescript: require('!!raw-loader!../../../app.component.ts')
      },
      {
        anchor: 'basic',
        title: 'Basic',
        desc: 'In order to use our toast properly, we need to invoke one of functions provided within ToastService such as success, warning, error, info.',
        component: BasicToastComponent,
        markup: require('!!raw-loader!./sections/basic-toast/basic-toast.component.html'),
        typescript: require('!!raw-loader!./sections/basic-toast/basic-toast.component.ts')
      },
      {
        anchor: 'timeout',
        title: 'Timeout',
        desc: 'We could simply set a timeout for a toast by adding timeOut from config and set the value in terms of number.',
        component: TimeoutToastComponent,
        markup: require('!!raw-loader!./sections/timeout-toast/timeout-toast.component.html'),
        typescript: require('!!raw-loader!./sections/timeout-toast/timeout-toast.component.ts')
      },
      {
        anchor: 'removeOnClick',
        title: 'Remove Toast On Click',
        desc: 'We could simply prevent a toast for being removed by adding removeOnClick from config and set the value to true.',
        component: PauseOnclickToastComponent,
        markup: require('!!raw-loader!./sections/pause-onclick-toast/pause-onclick-toast.component.html'),
        typescript: require('!!raw-loader!./sections/pause-onclick-toast/pause-onclick-toast.component.ts')
      }
    ];
  }

  private setRightMenu() {
    const apiDocs = [{
      anchor: 'toast-api-docs',
      title: 'API documentation',
      desc: null,
      component: null,
      markup: null,
      typescript: null
    }];
    const customRightMenu  = this.toastSection.concat(apiDocs);
    this.rightMenuSvc.setRightMenu(customRightMenu);
  }

}
