import { SizeButtonComponent } from './sections/size-button/size-button.component';
import { Component } from '@angular/core';
import { ShowcaseRightNavService } from '../../showcase-right-nav.service';
import { BasicButtonComponent } from './sections/basic-button/basic-button.component';
import { BlockButtonComponent } from './sections/block-button/block-button.component';
import { ActiveButtonComponent } from './sections/active-button/active-button.component';
import { IconButtonComponent } from './sections/icon-button/icon-button.component';
import { CombinationIconButtonComponent } from './sections/combination-icon-button/combination-icon-button.component';
import { LoaderButtonComponent } from './sections/loader-button/loader-button.component';
import { DisabledButtonComponent } from './sections/disabled-button/disabled-button.component';
declare var require: any;

@Component({
  selector: 'ugw-doc-button',
  templateUrl: './doc-button.component.html',
  styleUrls: ['./doc-button.component.scss']
})
export class DocButtonComponent {
  apiDocSection: any;
  description: string;
  buttonSections: Array<any>;
  title: string;

  constructor(private rightMenuSvc: ShowcaseRightNavService) {
    this.initializeMetaData();
    this.initializeApiDocumentation();
    this.initializeSection();
    this.setRightMenu();
  }

  private initializeApiDocumentation() {
    this.apiDocSection = {
      title: this.title.toLowerCase(),
      selector: 'pil-button',
      properties: [
        {
          name: 'disabled',
          description: 'When button disabled'
        },
        {
          name: 'active',
          description: 'When button active'
        }
      ]
    };
  }

  private initializeMetaData() {
    this.title = 'Button';
    this.description = 'Commonly used operating buttons, button combinations, button layouts.';
  }

  private initializeSection() {
    this.buttonSections = [
      {
        anchor: 'basic',
        title: 'Basic',
        desc: 'This is the most basic element in the component, you can quickly create a styled button.',
        component: BasicButtonComponent,
        markup: require('!!raw-loader!./sections/basic-button/basic-button.component.html'),
        typescript: require('!!raw-loader!./sections/basic-button/basic-button.component.ts')
      },
      {
        anchor: 'active',
        title: 'Active',
        desc: 'When the button is active.',
        component: ActiveButtonComponent,
        markup: require('!!raw-loader!./sections/active-button/active-button.component.html'),
        typescript: require('!!raw-loader!./sections/active-button/active-button.component.ts')
      },
      {
        anchor: 'disable',
        title: 'Disable',
        desc: 'When the button is disable.',
        component: DisabledButtonComponent,
        markup: require('!!raw-loader!./sections/disabled-button/disabled-button.component.html'),
        typescript: require('!!raw-loader!./sections/disabled-button/disabled-button.component.ts')
      },
      {
        anchor: 'icon',
        title: 'Icon',
        desc: 'icon button.',
        component: IconButtonComponent,
        markup: require('!!raw-loader!./sections/icon-button/icon-button.component.html'),
        typescript: require('!!raw-loader!./sections/icon-button/icon-button.component.ts')
      },
      {
        anchor: 'combination icon',
        title: 'combination icon',
        desc: 'Combination icon and text button.',
        component: CombinationIconButtonComponent,
        markup: require('!!raw-loader!./sections/combination-icon-button/combination-icon-button.component.html'),
        typescript: require('!!raw-loader!./sections/combination-icon-button/combination-icon-button.component.ts')
      },
      {
        anchor: 'loader',
        title: 'Loader',
        desc: 'Loader button.',
        component: LoaderButtonComponent,
        markup: require('!!raw-loader!./sections/loader-button/loader-button.component.html'),
        typescript: require('!!raw-loader!./sections/loader-button/loader-button.component.ts')
      },
      {
        anchor: 'size',
        title: 'Size',
        desc: 'Property to set the button size.',
        component: SizeButtonComponent,
        markup: require('!!raw-loader!./sections/size-button/size-button.component.html'),
        typescript: require('!!raw-loader!./sections/size-button/size-button.component.ts')
      },
      {
        anchor: 'block',
        title: 'Block',
        desc: 'Block buttom.',
        component: BlockButtonComponent,
        markup: require('!!raw-loader!./sections/block-button/block-button.component.html'),
        typescript: require('!!raw-loader!./sections/block-button/block-button.component.ts')
      }
    ];
  }

  private setRightMenu() {
    const apiDocs = [{
      anchor: 'button-api-docs',
      title: 'API documentation',
      desc: null,
      component: null,
      markup: null,
      typescript: null
    }];
    const customRightMenu  = this.buttonSections.concat(apiDocs);
    this.rightMenuSvc.setRightMenu(customRightMenu);
  }

}
