import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ShowcaseRightNavService {
  rightMenu: BehaviorSubject<any> = new BehaviorSubject<any>([]);

  constructor() { }

  setRightMenu(menu: any) {
    this.rightMenu.next(menu);
  }

  getRightMenu(): Observable<any> {
    return this.rightMenu.asObservable();
  }
}
