import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ElementRefService {
  sectionElRef: BehaviorSubject<any> = new BehaviorSubject<any>([]);
  rightNavElRef: BehaviorSubject<any> = new BehaviorSubject<any>([]);

  constructor() { }

  setSectionTitleCollectionRef(sectionElRef: any) {
    this.sectionElRef.next(sectionElRef);
  }

  getSectionTitleCollectionRef(): Observable<any> {
    return this.sectionElRef.asObservable();
  }

  setRightNavCollectionRef(rightNavElRef: any) {
    this.rightNavElRef.next(rightNavElRef);
  }

  getRightNavCollectionRef(): Observable<any> {
    return this.rightNavElRef.asObservable();
  }
}
